.form-control {}

.file-upload-btn:hover {
    background: #0070ba;
    color: #ffffff;
    transition: all 0.2s ease;
    cursor: pointer;
}

.file-upload-btn:active {
    border: 0;
    transition: all 0.2s ease;
}

.file-upload-content {
    display: none;
    text-align: center;
}

.file-upload-input {
    position: absolute;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    outline: none;
    opacity: 0;
    cursor: pointer;
}

.image-upload-wrap {
    margin-top: 20px;
    border: 1px dotted #0070ba;
    position: relative;
}

.image-dropping,
.image-upload-wrap:hover {
    background-color: lightgray;
    border: 1px dashed #0070ba;
    color: white;
}

.image-title-wrap {
    padding: 0 15px 15px 15px;
    color: #222;
}

.drag-text {
    text-align: center;
}

.drag-text h3 {
    font-weight: 100;
    text-transform: uppercase;
    color: #0070ba;
    padding: 60px 0;
}

.file-upload-image {
    max-height: 200px;
    max-width: 200px;
    margin: auto;
    padding: 20px;
}

.remove-image {
    width: 200px;
    margin: 0;
    color: #fff;
    background: #cd4535;
    border: none;
    padding: 10px;
    border-radius: 4px;
    border-bottom: 4px solid #b02818;
    transition: all 0.2s ease;
    outline: none;
    text-transform: uppercase;
    font-weight: 700;
}

.drop {
    border-color: black;
}

.cell {
    border: 0;
    white-space: nowrap;
    height: 55px;
    margin-top: 20px;
    padding-top: 20em;
    /* text-align: center; */
    margin-right: -0.09599em;
    margin: 0;
    outline: 0;
    vertical-align: middle !important;
    border: 0px !important;
    border-bottom: 0px !important;
}

@media screen and (min-width: 1px) and (max-width: 1365px) {
    .account {
        width: 100%;
